<template>
    <div>
        
        <van-nav-bar
            title="地址"
            left-arrow
            @click-left="$router.go(-1)"
        />

        <van-address-list
            v-model="chosenAddressId"
            :list="list"
            default-tag-text="默认"
            @add="onAdd"
            @edit="onEdit"
            @select="onSelect"
        />
    </div>
</template>

<script>
    export default {
        data() {
            return {
            chosenAddressId: '1',
            list: [],
            };
        },
        methods: {
            onAdd() {
                this.$router.push("/addressAdd")
            },
            onEdit(item) {
                this.$router.push({path:"/addressEdit",query:{id:item.id}})
            },
            onSelect(item){
                this.$store.commit("address/editId",item.id);
                this.$router.go(-1)
            }
        },
        created(){
            this.list = this.$store.state.address.lists || [];
            try {
                this.chosenAddressId = this.list.find(item=>item.isDefault).id
            } catch (error) {
                Toast('没有地址数据');
            }
            
        },
    };
</script>